.Navbar{
    position:absolute ;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
   
    width: 100%;
    height: 75px;
    
}
#navigation{
    display: flex;
    justify-content: space-evenly;
    column-gap: 3vw;
    color: rgb(223, 224, 226);
    margin-right: 1rem;
}
#logo{
    width: auto;
    height: 90%;
    filter:invert();
    
    
}
.Navbar a{
    color:rgb(223, 224, 226);
    text-decoration: none;
}
@media screen and (max-width: 600px) {
    #navigation{
        display: none;
    }
    .Navbar{
        justify-content: center;
    }
}